import React from 'react';
import MainHeader from 'views/mobileView/components/molecules/Headers/MainHeader';
import { Chip } from '@mui/material';
import Modal from 'react-bootstrap/Modal'
import ProgressBar from 'react-bootstrap/ProgressBar'
import db from 'libs/db';
import Footer from '../Footer';
import CurrencyInput from 'react-currency-input-field';

export default function MonthlyIncrease(props) {

	const [inflation, setInflation] = React.useState();
	const [nextDisabled, setNextDisabled] = React.useState(true);
	const [showTipModal, setShowTipModal] = React.useState(false);
	const STATE = props.location.state

	React.useEffect(async ()=>{
		await db.get(db.model.createGoal).then(p=>{
			if(p && p.inflation){
				setInflation(p.inflation)
			} else {
				setInflation("")
			}
		})
	},[])

	const checkValidation = () => {
		if (inflation >= 0) {
			setNextDisabled(false);
		} else {
			setNextDisabled(true)
		}
	};

	React.useEffect(() => {
		checkValidation();
	}, [inflation])

	const TipModal = () => {
		return (
			<Modal
				className="bottom-drawer"
				show={showTipModal}
				onHide={() => setShowTipModal(false)}
				keyboard={false}
			>
				<Modal.Body>
					<div className='header1dark'>Inflation rates</div>
					<ul>
						{
							STATE.goalRelationScreen.tip.map((a, i) => {
								return <li key={i} style={{ margin: "4% 0" }} className='bodyTextLight'>{a.key}
									<span className='bodyTextDark'> {a.value}</span></li>
							})
						}
					</ul>
				</Modal.Body>
				<Modal.Footer>
					<button className="solidButton" onClick={() => setShowTipModal(false)}>
						Okay
					</button>
				</Modal.Footer>
			</Modal>
		)
	}

	if (STATE === undefined) {
		props.history.push('/Goals')
		return <></>
	}
	return (
		<div className='mainContainer'>
			<MainHeader
				header={STATE.header}
				navigation={props.history}
				right='tip'
				rightAction={() => setShowTipModal(true)}
			/>
			<ProgressBar now={80} className="progress-bar-custom" />
			<div className='dataContainer'>
				<div className="bodyTextDark" style={{ marginTop: "6%" }}>{STATE.goalRelationScreen.question}</div>
				{/* <input
					type="tel"
					id="inflation"
					className='normalInput'
					placeholder={STATE.goalRelationScreen.placeholder}
					value={inflation}
					onChange={(num) => {
						const a = Number(num.target.value.replace(/\D/g, ''))
						setInflation(a);
					}}
				/> */}
				<CurrencyInput
					className='normalInput'
					id="inflation"
					name="inflation"
					placeholder={STATE.goalRelationScreen.placeholder}
					value={inflation}
					allowDecimals={true}
					allowNegativeValue={false}
					disableAbbreviations={false}
					suffix=' %'
					onValueChange={(value) => setInflation(Number(value) || 0)}
					autoFocus
					/>
				<div className='hiddden-scrollbar'>
					{STATE.goalRelationScreen.chips.map((item) => {
						return (
							<Chip
								className={inflation === item.key ? "normalChip selected" : "normalChip"}
								key={item.key}
								label={item.name}
								onClick={() => {
									setInflation(item.key)
								}}
							/>
						)
					})}
				</div>
			</div>
			<div className='footer'>
				<button
					className='solidButton'
					style={{ opacity: nextDisabled ? 0.4 : 1 }}
					onClick={() => {
						if (!nextDisabled) {
							props.history.push({ pathname:'/Goals/goal-name', state: { ...STATE, inflation } })
						}
					}}

				>Next</button>
			</div>
			<Footer
				navigation={props.history}
				next='/Goals/goal-name'
				state={{ ...STATE, inflation }}
				saveData = {{ inflation }} 
				buttonText="Next"
				nextDisabled={nextDisabled}
			/>
			<TipModal />
		</div>
	)
}
